.main {
  background-color: rgb(236, 236, 245);
  height: 100%;
  width: 100%;
  color: black;
  text-align: center;
  padding: 20px;
}
.skillsDiv > div {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 90%;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  margin-left: 5%;
  padding: 20px;
  margin-top: 30px;
}

.imagesDiv > div > img {
  height: 90%;
  width: 80%;
}

.Toolhead {
  margin-top: 20px;
}

.ToolsDiv > div {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 95%;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  gap: 10px;
  padding: 20px;
  margin-top: 30px;
  margin-left: 28px;
}
.gitHead {
  margin-top: 70px;
}
.days {
  margin-top: 20px;
}

.gitCalender {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;

  background-color: rgb(236, 236, 245);
  margin-top: 30px;
  width: 75%;
  padding-left: 25px;
  margin-left: 180px;
  padding-bottom: 20px;
  padding-top: 10px;
}

.stats {


  width: 70%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  margin-top: 80px;
}
.stats2 {

  margin-top: 60px;
}

.ToolsimagesDiv > div > img {
  height: 90%;
  width: 90%;
}

.Toolhead {
  margin-top: 50px;
}

.ToolsimagesDiv > div > p {

  margin-top: 3px;
  width: 100%;
}


@media screen and (min-width: 500px) and (max-width: 1100px) {
  .skillsDiv > div {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 65px;
  }
  .imagesDiv > div > img {
    margin-top: 20px;
    height: 70%;
    width: 60%;
  }
  .ToolsDiv > div {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 50px;
    width: 93%;
  }
  .ToolsimagesDiv > div > img {
    height: 85%;
    width: 70%;
  }
  .gitCalender {
    width: 80%;
    margin: auto;
    padding: 30px;
  }
  .githubimage {
    block-size: 13px;
    margin-block: 13px;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
  .image {
    width: 70%;
  }
  .stats2 {
    width: 40%;
  }
  .ToolsimagesDiv > div {
    margin-top: 10px;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .skillsDiv > div {
    grid-template-columns: repeat(2, 1fr);
    margin: auto;
  }

  .imagesDiv > div > img {
    margin-top: 20px;
    height: 70%;
    width: 60%;
    padding: 3px;
  }

  .ToolsDiv > div {
    grid-template-columns: repeat(2, 1fr);
    margin: auto;
    width: 85%;
  }
  .ToolsimagesDiv > div > img {
    height: 85%;
    width: 70%;
  }
  .gitCalender {
    width: 85%;
    margin: auto;
    padding: 30px;
    font-size: 11px;
  }
  .githubimage {
    block-size: 13px;
    font-size: 14px;
    height: 40%;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
  .image {
    width: 90%;
  }
  .stats2 {
    width: 50%;
  }
  .ToolsimagesDiv > div {
    margin-top: 22px;
  }
}
