.main {
  height: 100%;
  width: 100%;
  background-color: rgb(236, 236, 245);
  color: black;
  padding: 20px;

}
.mainhead {
  color: black;
  text-align: center;
  margin-top: 30px;

}

.project {
  box-shadow:rgb(147, 189, 232) 0px 3px 8px;
  width: 90%;
  height: 500px;
  margin: auto;
  color: black;
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  gap: 30px;
  
}

.bloommedplus,
.crystallvills,
.lyst {
  height:100%;
  width: 100%;
  display: block;
}

.description {
  width: 90%;
  height: 490px;
  margin-left: 40px;
  text-align: center;
}

.head {
  margin-top: 10px;
  color: black;
}

.prjctDes1 {
  margin-top: 30px;
}

.tech {
  margin-top: 30px;
  color: black;
}

.lists {
  width: 50%;
  height: 200px;
  margin: auto;
}

.techs {
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.techs > h4 {
  box-shadow:rgb(147, 189, 232) 0px 3px 8px;
  width: 80%;
  margin: auto;
  padding: 3px;
  margin-top: 20px;
}

.links {
 
  margin-top: 50px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.githubLink,
.link,
.vdo {
  border: 2px solid #1e90ff;
  background-color: black;
  color: white;
  
  width: 100px;
  height: 40px;
  
  font-size: 20px;
  cursor: pointer;
}

.Imgdiv{

  height: 100%;
  margin: auto;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .project {
    grid-template-columns: repeat(1, 1fr);
    height: 100%;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .project {
    grid-template-columns: repeat(1, 1fr);
    height: 850px;
  }
  .bloommedplus,
  .crystallvills,
  .lyst {
    
    width: 100%;
   
  }
  .description {
    margin: auto;
  }
  .techs {
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  .techs > h4 {
    width: 97%;
  }
}
