.main_nav {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  background-color: rgb(236, 236, 245);
  height: 60px;
  width: 100%;
  color: black;
  position: fixed;
  top: 0;
}
.navbar {
  width: 100%;
  margin: 5px;
  padding-left: 50rem;
}

.box {
  margin: auto;
  text-align: center;
  padding: 10px;
  font-weight: bold;
}
.box:hover {
  cursor: pointer;
  border-bottom: 3px solid #1e90ff;
}

.resume {
  padding: 10px;

  border: none;
  background-color: rgb(236, 236, 245);
  color: black;
  font-weight: bold;
  font-size: 15px;
}

.drawer {

  transition: left 0.5s ease;
  padding:10px;
  margin-top: 60px;
  display: none;
}

.content {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  height: 230px;
  width:80px;
  background-color: rgb(236, 236, 245);
  color: black;
  margin-top: 58px;
}
.close {
  
  background-color: rgb(236, 236, 245);
  border: none;
  color: black;
  padding-top: 15px;
  width: 80%;
  padding-left: 25%;
}
.body {
  width: 50%;
  font-weight: bold;
  margin-top: 20px;
 padding-left: 30px;
}

.iconBtn {
  height: 30px;
  width: 30px;
  visibility: hidden;
  margin-left: -30px;
  background-color: rgb(236, 236, 245);
}

.hamburger {
  height: 60px;
  width: 100px;
}

.logo_nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  margin-left: 30px;
  font-style: italic;
  padding-right: 3px;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
   .navbar {
    flex-direction: column;
  } 
  .iconBtn {
    height: 30px;
    width: 30px;
    visibility: visible;
    margin-left: -30px;
  }
  .logo_nav {
    padding-top: 12px;
  }

  .iconBtn {
    padding-right: 30px;
  } 

/* .nav2{
   
  } */

  .navbar{
    flex-direction: column;
    border: 2px solid red;
  }


}
@media screen and (min-width: 100px) and (max-width: 500px) {
  .navbar {
    display: none;
  }
  .iconBtn {
    margin: 15px;
    height: 30px;
    width: 30px;
    visibility: visible;
  
  }
}
