.main {
  /* background-color: rgb(207, 214, 222); */
  background-color: rgb(236, 236, 245);
  height: 100vh;
  width: 100%;
  
  margin-top: 4%;
}

.head {

  text-align: center;
  margin: auto;
  padding: 15px;
  color: black;
}
.imgDiv {
  top: 0;
  bottom: 0;

  height: 100%;
  width: 100%;

  margin-top: 4%;
  filter: blur(2px);
  -webkit-filter: blur(2px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.girl {
  height: 100%;
  width: 100%;
}

.name {
  
  background-color: rgb(236, 236, 245);
  width: 50%;
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  padding: 15px;
  position: absolute;
  margin-top: -30%;
  color: white;
  font-weight: bold;
  font-size: 23px;
  text-align: center;
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .head {
    margin: auto;
    font-size: 18px;
  }
  .main {
    width: 100%;
    flex-direction: column-reverse;
    margin-top: 60px;
  }
  .imgDiv {
    width: 100%;
  }
  .name {
    padding: 30px;
    width: 100%;
  }
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .head {
    text-align: center;
  }
  .main {
    flex-direction: column-reverse;
  }
  .imgDiv {
    width: 100%;
  }
  .name {
    padding: 30px;
    width: 100%;
  }
}
