.main {
  background-color: rgb(236, 236, 245);
  color: black;
  height: 100%;
  width: 100%;
  padding: 20px;
  
}
.aboutDiv {
  
  margin: auto;
  text-align: center;
  padding: 20px;
}

.description {
 
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  height: 100%;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  text-align: center;
  padding: 30px;
  margin: auto;

}

.rani {
  height:400px;
  border-radius: 50%;
  margin: 10px;
}

.resume {
  background-color: black;
  color: white;
  border: 2px solid #1e90ff;
  font-size: 20px;
  font-weight: bold;
  height: 40px;
  width: 100px;
  margin: 30px;
}

.resume:hover {
  cursor: pointer;
}
.lines {
  padding: 10px;
}

.lines > p {
  font-size: 25px;
}

.hi {
  padding: 20px;
}

.para {
  text-align: start;
}





@media screen and (min-width: 500px) and (max-width: 1100px) {
  .main {
    height: 100%;
    width: 100%;
  }
  .description {
    height: 90%;
    width: 88%;
    flex-direction: column;
    font-size: 12px;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .main {
    width: 100%;
    height: 100%;
  }
 
  .rani {
    height: 220px;
  }

  .lines > p {
    font-size: 13px;
  }
  .resume {
    height: 40%;
    font-size: 14px;
  } 
  .description {
   
    width: 75%;
    height: 100%;
    flex-direction: column;
  }
}
